/// <reference path="../main.scss" />

////
/// PRETTYPRINT
////

/// VARIABLES
$PRETTIFY-NoCode:               $COLOR-Black;
$PRETTIFY-String:               $COLOR-YellowLight;
$PRETTIFY-Keyword:              $COLOR-Cyan;
$PRETTIFY-Comment:              $COLOR-GreyDark;
$PRETTIFY-Type:                 $COLOR-Cyan;
$PRETTIFY-Literal:              $COLOR-Purple;
$PRETTIFY-Punctuation:          $COLOR-GreyLighter;
$PRETTIFY-Plaintext:            $COLOR-GreyLighter;
$PRETTIFY-Tag:                  $COLOR-Pink;
$PRETTIFY-Attribute:            $COLOR-Emerald;
$PRETTIFY-AttributeValue:       $COLOR-YellowLight;
$PRETTIFY-Decimal:              $COLOR-GreyLighter;
$PRETTIFY-Emphasis:             $COLOR-GreyLight;

@if $UTILITY-PRETTYPRINT {

    // Monokai Sublime style.
    // Derived from Monokai by noformnocontent http://nn.mit-license.org/

    .prettyprint {
        .hljs {
            display: block;
            overflow: hidden;
            overflow-x: auto;
            padding: 1em;
            color: $COLOR-GreyLighter;
            background-color: $COLOR-GreyDarker;
            -webkit-text-size-adjust: none;
        }

        .hljs,
        .hljs-tag,
        .css .hljs-rule,
        .css .hljs-value,
        .aspectj .hljs-function,
        .css .hljs-function,
        .hljs-preprocessor,
        .hljs-pragma {
            color: $PRETTIFY-Plaintext;
        }

        .hljs-strongemphasis,
        .hljs-strong,
        .hljs-emphasis {
            color: $PRETTIFY-Emphasis;
        }

        .hljs-bullet,
        .hljs-blockquote,
        .hljs-horizontal_rule,
        .hljs-number,
        .hljs-regexp,
        .alias .hljs-keyword,
        .hljs-literal,
        .hljs-hexcolor {
            color: $PRETTIFY-Literal;
        }

        .hljs-tag .hljs-value,
        .hljs-code,
        .hljs-title,
        .css .hljs-class,
        .hljs-class .hljs-title:last-child {
            color: $PRETTIFY-Attribute;
        }

        .hljs-link_url {
            font-size: 80%;
        }

        .hljs-strong,
        .hljs-strongemphasis {
            font-weight: bold;
        }

        .hljs-emphasis,
        .hljs-strongemphasis,
        .hljs-class .hljs-title:last-child,
        .hljs-typename {
            font-style: italic;
        }

        .hljs-keyword,
        .ruby .hljs-class .hljs-keyword:first-child,
        .ruby .hljs-function .hljs-keyword,
        .hljs-function,
        .hljs-change,
        .hljs-winutils,
        .hljs-flow,
        .nginx .hljs-title,
        .tex .hljs-special,
        .hljs-header,
        .hljs-attribute,
        .hljs-symbol,
        .hljs-symbol .hljs-string,
        .hljs-tag .hljs-title,
        .hljs-value,
        .alias .hljs-keyword:first-child,
        .css .hljs-tag,
        .css .unit,
        .css .hljs-important {
            color: $PRETTIFY-AttributeValue;
        }

        .hljs-function .hljs-keyword,
        .hljs-class .hljs-keyword:first-child,
        .hljs-aspect .hljs-keyword:first-child,
        .hljs-constant,
        .hljs-typename,
        .hljs-name,
        .css .hljs-attribute {
            color: $PRETTIFY-Keyword;
        }

        .hljs-variable,
        .hljs-params,
        .hljs-class .hljs-title,
        .hljs-aspect .hljs-title {
            color: $PRETTIFY-Plaintext;
        }

        .hljs-string,
        .css .hljs-id,
        .hljs-subst,
        .hljs-type,
        .ruby .hljs-class .hljs-parent,
        .hljs-built_in,
        .django .hljs-template_tag,
        .django .hljs-variable,
        .smalltalk .hljs-class,
        .django .hljs-filter .hljs-argument,
        .smalltalk .hljs-localvars,
        .smalltalk .hljs-array,
        .hljs-attr_selector,
        .hljs-pseudo,
        .hljs-addition,
        .hljs-stream,
        .hljs-envvar,
        .apache .hljs-tag,
        .apache .hljs-cbracket,
        .tex .hljs-command,
        .hljs-prompt,
        .hljs-link_label,
        .hljs-link_url {
            color: $PRETTIFY-Tag;
        }

        .hljs-comment,
        .hljs-javadoc,
        .hljs-annotation,
        .hljs-decorator,
        .hljs-pi,
        .hljs-doctype,
        .hljs-deletion,
        .hljs-shebang,
        .apache .hljs-sqbracket,
        .tex .hljs-formula {
            color: $PRETTIFY-Comment;
        }

        .coffeescript .javascript,
        .javascript .xml,
        .tex .hljs-formula,
        .xml .javascript,
        .xml .vbscript,
        .xml .css,
        .xml .hljs-cdata,
        .xml .php,
        .php .xml {
            opacity: 0.5;
        }

        *::selection {
            color: $COLOR-GreyLighter;
            background: $COLOR-PinkDarker;
        }

        *::-moz-selection {
            color: $COLOR-GreyLighter;
            background: $COLOR-PinkDarker;
        }
    }
}
